/* ================================================== */
body{
    padding: 0;
    margin: 0;
    background: rgb(255, 140, 140)  url("../assets/PIKIMELL.svg") no-repeat scroll 50% 50%;
    font-family: 'Roboto',sans-serif;
    font-weight: 600;
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    box-sizing: border-box;
}

.box{
    backdrop-filter: blur(20px);
    background: #ffffff6c;
    border-radius: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 20px;
    min-width: 20vw;
    min-height: 20vh;
    margin: 10px;
}
/* ================================================== */

/* ================================================== */
.control-container{
    overflow-y: auto;
    width: 20vw;
    height: 80vh;
    max-height: 80vh;
    max-height: 80vh;
    flex-direction: column;
}

.control-container > * {
    margin:10px 0;
}

.control-container > button{
    width: 70%;
}
/* ================================================== */


/* ================================================== */
.canvas-container{
    overflow: auto;
    width: 60vw;
    height: 80vh;
    max-width: 60vw;
    max-height: 80vh;
    justify-content: start;
    align-items: flex-start;
    border-radius: 0;
    box-sizing: border-box;

}

.canvas-container > canvas{
    border-radius: 10px;
    padding: 20px;
    
}
/* ================================================== */
